* {
  padding: 0;
  margin: 0;
}

/*网页字体和颜色*/

html {
  color: #333333;
  background: #FFFFFF;
}

/* 去掉 table cell 的边距并让其边重合 */

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* 去除默认边框 */

img {
  border: none;
  vertical-align: middle;
  border: 0;
}

/*input输入文字不贴边*/

input::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #A8A8A8;
}

input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #A8A8A8;
}

input::-ms-input-placeholder {
  /* Internet Explorer 10+ */
  color: #A8A8A8;
}

input,
optgroup,
select,
textarea {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-user-modify: read-write-plaintext-only;
  outline: none;
}

button {
  cursor: pointer;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  outline: none;
}

/* 去掉列表前的标识，li 会继承 */

ol,
ul {
  list-style: none;
}

/* 默认不显示下划线，保持页面简洁 */

ins,
a {
  text-decoration: none;
}

a {
  color: #333333;
}

a:focus {
  outline: none;
}

/*显示隐藏*/

.hide {
  display: none;
}

.block {
  display: block;
}

/* 设置浮动，减少浮动带来的 bug */

.fl,
.fr {
  display: inline;
}

/*左浮动*/

.fl {
  float: left;
}

/*右浮动*/

.fr {
  float: right;
}

/*清除浮动*/

.clearfix:after {
  display: block;
  clear: both;
  content: "";
  visibility: hidden;
  height: 0;
}

.clearfix {
  zoom: 1;
}

.center {
  text-align: center;
}

.left {
  text-align: left;
}

.right {
  text-align: right;
}

.cursor {
  cursor: pointer;
}

.bold {
  font-weight: bold;
}

.bor0 {
  border: none !important;
}

.backbgnone {
  background: none !important;
}

.windows {
  position: fixed;
  z-index: 999999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.3);
}

.windows::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.red {
  color: #FF5A59;
}